import React,{useState, useEffect} from "react";
import { useDispatch, useSelector } from 'react-redux'
import { attendanceDashboardActions } from '../core/resource/actions';
import AllAttendanceLog from "./modal/attendanceDashboard/AllAttendanceLog";
import {addInMomentDate} from '../utils/AppHelper'

const AlertMessage = () => {
  const dispatch = useDispatch()
  const { getCheckInCheckOutPending,
          getCheckInCheckOutRes,
          getCheckInCheckOutError
         } = useSelector(state => state.attendanceDashboard)

  const [getAttendanceLog, setAttendanceLog] = useState()
  const [attendanceTime, setAttendanceTime  ] = useState()
  const [timer, setTimer] = useState('00:00:00');
  const [breakTime,setBreackTime] = useState('00:00:00')
  const [totalWorkingHours, setTotalWorkingHoues] = useState("00:00:00")
  const [openLogModel, setOpenLogModel] = useState(false)
  const [attendanceAllLog,setAttendanceAllLog] = useState([])


  const openModelAllLog = ()=>{
    getCheckInCheckOutdata()
    setOpenLogModel(!openLogModel)
  }


  const getCheckInCheckOutdata = ()=>{
    dispatch(attendanceDashboardActions.getCheckInCheckOutRequest())
  }

  useEffect(() => {
    if (!getCheckInCheckOutPending) {
        if (getCheckInCheckOutError) {
            return;
        }
        if (getCheckInCheckOutRes) {
            if (getCheckInCheckOutRes?.data?.code === 200) {
              const checkInCheck = getCheckInCheckOutRes?.data?.data
              setAttendanceLog(checkInCheck) 
              setAttendanceTime(checkInCheck?.attendanceTime)
              setAttendanceAllLog(checkInCheck?.attendanceAllLog)
              const {
                firstEntryTime,
                workHours,
                workMinutes,
                workSeconds,
                breakHours,
                breakMinutes,
                breakSeconds,
                totalBreakTime,
                totalWorkingTime

              } = checkInCheck?.attendanceTime
              if (firstEntryTime) {
              let workTime = `${workHours}:${workMinutes}:${workSeconds}`;
              setBreackTime(totalBreakTime);
              setTotalWorkingHoues(totalWorkingTime)

              const intervalFun = setInterval(() => {
                workTime = addInMomentDate(workTime,'hh:mm:ss',1,"seconds","HH:mm:ss")
                setTimer(workTime);
              }, 1000);
              return () => clearInterval(intervalFun);
            }
            }
        }
    }
}, [
    getCheckInCheckOutRes,
    getCheckInCheckOutError,
    getCheckInCheckOutPending
]);

  return (
    <>
    <div className="alertsSec cardBox mb-3 p-3">
      <h3 className="fs-6 fw-regular text-orange d-flex align-items-center gap-1 mb-0 me-4">
        <img src="images/alert.svg" alt="" width={15} />
        Alerts
      </h3>
      <div className="alertList dashboardAlrt pt-2">
        <marquee>
          <a className="pe-2">
            Hey Nikita Prajapat, your attendance correction of{" "}
            <span className="text-danger"> 12 Feb 2024</span> will get locked by
            today, please check your calendar and do corrections if required
            before 12 AM
          </a>
        </marquee>
      </div>
      <div class="timerButtonGroup d-flex gap-2">
        <button type="button" class="btn btn-primary d-flex align-items-center justify-content-center gap-2"
          onClick={()=>openModelAllLog()} ><img src="images/timer-icon.svg" alt=""
            width="18" /> {timer}</button>
        <button type="button"
          class="btn btn-secondary d-flex align-items-center justify-content-center gap-2"><img
            src="images/break-icon.svg" alt="" width="18" /> {breakTime}</button>
      </div>
    </div>
    {openLogModel &&
    <AllAttendanceLog
    openLogModel={openLogModel}
    setOpenLogModel={setOpenLogModel}
    attendanceAllLog={attendanceAllLog}
    breakTime={breakTime}
    totalWorkingHours={timer}
    attendanceTime={attendanceTime}
    />}
    </>
  );
};

export default AlertMessage;
